:root {
  --box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  --card-shadow: rgba(228, 228, 234, 0.22) 0 22px 25px 0, rgba(228, 228, 234, 0.5) 0 9px 23px 0;
  --card-shadow-hover: rgba(228, 228, 234, 0.56) 0 22px 32px 0, rgba(228, 228, 234, 0.64) 0 9px 28px 0;
  --transition: all 0.3s ease-in-out;
  --font: 'Lato', sans-serif;
  --font2: 'Poppins', sans-serif;
  --sidebar: 250px;
  --hover: rgba(216, 216, 233, 0.2);
  --title: #394963;
  --text: #7f8897;
  --grey: #e7e7e7;
  --headerHeight: 70px;
  --primary: #6e61ff;
  --secondary: #3bdef2;
  --text-inverse: #fff;
  --gradient1: linear-gradient(45deg, #d176ff, #9f6bff);
  --gradient1: linear-gradient(45deg, #28f4f5, #84abf3);
  --gradient2: linear-gradient(45deg, #fff, #a165ff);
  --primary-shadow: rgba(101, 219, 255, 0.21);
  --circle: rgba(36, 188, 211, 0.03);
  --header-background-color: rgba(1, 1, 1, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  margin: 0;
  position: absolute;
  width: 100vw;
}

main {
  width: 100%;
  position: relative;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title);
  font-weight: 700;
}

h2,
h3 {
  margin-bottom: 0;
}

h1 {
  font-family: var(--font2);
  color: var(--title);
  margin-bottom: 0;
  max-width: 450px;
}

h2 {
  margin-top: 0;
}

p {
  font-family: var(--font2);
}



* {
  font-family: var(--font);
  color: var(--text);
}

p * {
  font-family: var(--font2);
}

button:not(.navbtn) {
  padding: 10px 20px 10px 20px;
  border: 0;
  outline: 0;
  border-radius: 2px;
  background: var(--primary);
  cursor: pointer;
  color: var(--text-inverse);
  box-shadow: 0 10px 30px var(--primary-shadow);
  -webkit-transition: var(--transition);
  transition: var(--transition);
  -webkit-transition: var(--transition);
  font-family: var(--font2);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

button:not(.navbtn):hover {
  filter: brightness(0.9);
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}

header {
  z-index: 10;
  -webkit-transition: var(--transition);
  transition: var(--transition);
  -webkit-transition: var(--transition);
  justify-content: center;
  align-items: center;
  background-color: var(--header-background-color);
}

@keyframes circle-ripple {
  from {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }

  to {
    opacity: 0;
    transform: scale3d(3, 3, 1);
  }
}

a {
  text-decoration: none;
  color: var(--primary);
  position: relative;
  display: inline-block;
}

a::after {
  background: none repeat scroll 0 0 transparent;
  background: var(--primary);
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

a:hover::after {
  width: 100%;
  left: 0;
}
